import React, { Component } from 'react';
import Axios from 'axios';
export default class PopUp extends Component {
    state={
        name: '',
        imageUrl: '',
        pokemonIndex: '',
        abilities:'',
        height:'',
        weight:''
    };

    
async componentDidMount(){
    const pokemonIndex =this.props.index;
    console.log(pokemonIndex);
    const pokemonUrl = `https://pokeapi.co/api/v2/pokemon/${pokemonIndex}/`;
    const pokemonRes = await Axios.get(pokemonUrl);
    const imageUrl = pokemonRes.data.sprites.front_default;
    const name = pokemonRes.data.name;
    const height=pokemonRes.data.height;
    const weight=pokemonRes.data.weight;
    const abilities=pokemonRes.data.abilities.map(ability=>{
        return ability.ability.name
          .toLowerCase()
          .split('-')
          .map(s => s.charAt(0).toUpperCase() + s.substring(1))
          .join(' ');
    }).join(', ');

this.setState({name,height,weight,abilities,imageUrl})
}
  render() {
    return (
      <div className="modal-body">
      <div className="modal-dialog">
       <div className="modal-content">
        <h4>{this.state.name}</h4>
        <img src={this.state.imageUrl} className="card-img-top rounded mx-auto mt-2"/>
    <h6 className="float-right">Height: {this.state.height}</h6>
    <h6 className="float-right">Height: {this.state.weight}</h6>
    <h6 className="float-right">Abilities: {this.state.abilities}</h6>
       </div>
     </div>
     </div>
    );
   }
  }