import React, { Component } from 'react';
import PokemonCard from './PokemonCard';
import axios from 'axios';



export default class PokemonList extends Component {
  token=null;
  state = {
    pokemon:null,
    input_val: ""
  };



// async componentDidMount() {
//     console.log('I was triggered during componentDidMount');
//     this.search("");
//     const res = await axios.get(this.state.url);
//     console.log(res);
//     this.setState({ pokemon: res.data['results']});
//   }

componentDidMount() {
    console.log('I was triggered during componentDidMount');
    this.search("");
  }

async search(query){
  query=query.toLowerCase();
  const new_url=`https://pokeapi.co/api/v2/pokemon/${query}`
  const token={};
  this.token=token;
  await axios.get(new_url).then((res)=>{
    if (this.token==token){
      if (query==""){
      this.setState({pokemon:res.data['results']});
      }else{
        try{
        // console.log(res.data['forms'][0]['url']);
        let temp=res.data['forms'][0]['url'].split("/");
        let id=temp[temp.length-2];

        this.setState({pokemon:[{name:query,url:`https://pokeapi.co/api/v2/pokemon/${id}`}]})
        }catch{
          this.setState({pokemon:null});
        }
      }
    }
  });

}

  filterOnChange=(event)=>{
      this.setState({input_val: event.target.value});
      this.search(event.target.value);
  }
  render() {

    if (this.state.input_val!==""){
        this.search(this.state.input_val);
    }
    return (
      <div>
          <div className="row">
            <label htmlFor="search">Search by name</label>
            <input type="text" onChange={this.filterOnChange}/>

          </div>
          {this.state.pokemon ? (
          <div className="row">
            {this.state.pokemon.map(pokemon => (
              <PokemonCard
                key={pokemon.name}
                name={pokemon.name}
                url={pokemon.url}
              />
            ))}
          </div>
        ) : (
          <h4>Loading...</h4>
        )}
      </div>
    );
  }
}